.TimeLineChartComponent .axis.axis--x line {
  stroke: #c5c5c5; }

.TimeLineChartComponent .axis.axis--x path {
  stroke: #c5c5c5; }

.TimeLineChartComponent .axis.axis--x text {
  fill: #c5c5c5; }

.TimeLineChartComponent .thread line {
  stroke: #333333;
  stroke-width: 2px; }

.TimeLineChartComponent .thread .event circle {
  stroke: #333333;
  stroke-width: 2px; }

.TimeLineChartComponent .thread .event text {
  font-size: 14px;
  font-family: "Source Sans Pro", sans-serif;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none; }



